.faq {
  display: flex;
  justify-content: center;
}

.faq__wrapper {
  max-width: 1366px;
  padding: 0 198px;
  width: 100%;

  table {
    border-collapse: collapse;
    width: 100%;
  }

  caption {
    text-align: center;
    padding-bottom: 49px;
    font-family: "Cormorant Garamond", "Times New Roman", serif;
    font-size: 34px;
    font-weight: 700;
    line-height: 44.2px;
    letter-spacing: 0.03em;
    margin: 0;
  }

  tr:hover {
    background-color: $light-brown;
  }

  td {
    border: 1px solid $brown;
    padding: 43px 100px 44px 46px;
    position: relative;
  }

  h3 {
    font-size: 18px;
    line-height: 23.4px;
    font-weight: 500;
    margin: 0;
  }

  p {
    font-weight: 400;
    padding-top: 9px;
    padding-left: 5px;
    margin: 0;
    padding-right: 300px;
    line-height: 20.8px;
  }

  button {
    display: block;
    position: absolute;
    content: "";
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    border: 0;
    background-color: transparent;

    &::before {
      position: absolute;
      content: "";
      width: 17px;
      height: 10px;
      right: 47px;
      top: 49px;
      background-image: url("../img/icon-arrow-up.svg");
    }
  }
}

.faq__list-item--hide button {
  &::before {
    display: none;
  }

  &::after {
    position: absolute;
    content: "";
    width: 17px;
    height: 10px;
    right: 47px;
    top: 53px;
    background-image: url("../img/icon-arrow-down.svg");
  }
}

.faq__list-item--hide p {
  display: none;
}

.faq--nojs {
  p {
    display: block;
  }

  button {
    &::before {
      display: none;
    }

    &::after {
      display: block;
    }
  }
}

@media (max-width: $tablet-width) {
  .faq__wrapper {
    padding: 0 45px;

    p {
      padding-right: 0;
      padding-top: 26px;
      padding-left: 2px;
    }

    h3 {
      padding-left: 2px;
      letter-spacing: 0.003em;
    }

    td {
      padding-bottom: 44px;
    }

    button {
      &::before {
        width: 26px;
        height: 16px;
        right: 49px;
        top: 47px;
        background-size: cover;
      }

      &::after {
        width: 26px;
        height: 16px;
        right: 49px;
        top: 47px;
        background-size: cover;
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .faq__wrapper {
    padding: 0 15px;

    caption {
      padding-bottom: 27px;
    }

    td {
      padding: 19px 58px 18px 24px;
    }

    h3 {
      font-size: 16px;
      line-height: 22.4px;
      padding-right: 0;
      padding-left: 0;

      &:last-child {
        padding-right: 0;
      }
    }

    p {
      font-size: 14px;
      line-height: 20.6px;
      padding-top: 13px;
      padding-left: 0;
      padding-right: 0;
    }

    button {
      &::before {
        width: 23px;
        right: 23px;
        top: 23px;
        height: 13px;
      }

      &::after {
        width: 23px;
        height: 13px;
        right: 23px;
        top: 23px;
      }
    }
  }

  .faq__list-item--return h3 {
    padding-right: 0;
  }
}
